const MenuContainer = (props) => {
    let {  data, options ,i } = props;

    if(data.options === undefined ){
        props.getOptions(i, initOptions)
    }
    return(
        <div className={"menu-container menu-container-" + options.type }>
            {options.items ? 
                options.items.map((item, i) => {
                return <MenuItem item={item} type={options.type} key={i}/>
                })
            : '' }
            
            {/* eslint-disable-next-line  */}
            {options.type == 4 ? <a href={options.btnLink} className="pc"><Button>Shop Now</Button></a> : null}
        </div>
    )
}
   
