import React from "react";
import "./TitleSubheading.scss";
import { defaultValues } from "../../constants";

const defaultProps = {
  title: defaultValues.HERO_TITLE,
  label: defaultValues.DESCRIPTION,
};

const TitleSubheading = (props) => {
  let { title, label } = props;
  return (
    <div className="title-subheading">
        <h2 className="Header-2">{title}</h2>
        <p className="Body-1-Regular">{label}</p>
    </div>
  );
};

TitleSubheading.defaultProps = defaultProps;

export default TitleSubheading;
