import React from "react";
import { defaultValues } from "../../constants";
import Button from '../../atoms/Button/Buttons'
import TitleSubheading from "../../atoms/Product/TitleSubheading";
import ProductFeature from "../../atoms/Product/ProductFeature";
import TempImage from "../../TempImage";

const initOptions = {
  title: defaultValues.HERO_TITLE,
  description: defaultValues.DESCRIPTION,
  subDescription: defaultValues.DESCRIPTION,
  imgFn: defaultValues.PRODUCT_SQUARE_IMAGE,
  src: defaultValues.PRODUCT_SQUARE_IMAGE,
  subtitle: 'This is a subtitle',
  buttonTitle: 'Shop Now',
  buttonSrc:'/',
  type: '1',
  types:{
      1: '',
      2: '',
  }
}


const Products_3_ImageLeft = (props) => {
  let {
    data,
    i,
    options
} = props;


  if(data.options === undefined ){
    props.getOptions(i, initOptions)
}
  return (
    <div>
      <div className={"products products-3 " + (options.title.toLowerCase().replace(/[^\w\s]/gi, '-').replace(/ /g, '-'))+(options.type == 2 ? ' product-right' : '')}>
        <TempImage scale="50%" title={options.title + ' Feature'} src={options.imgFn}/>
        <div className="product-body ">
          <TitleSubheading title={options.title}/>
          <ProductFeature className="showPC" title={options.subtitle} description={options.subDescription}/>
            <a href={options.buttonSrc} className="js-tracking product-button" data-label={options.buttonTitle.toLowerCase().replace(/ /g, '_') + '_cta'}>
                <Button  >{options.buttonTitle}</Button>
            </a>
        </div>
      </div>
      <div className="products products-3 showTB">
        <div className="product-body ">
          <ProductFeature className="" title={options.subtitle} description={options.subDescription}/>
        </div>
      </div>
     
    </div>

  );
};

Products_3_ImageLeft.defaultProps = {
  options:{
    title: defaultValues.HERO_TITLE,
    subtitle: 'This is a subtitle',
    description: defaultValues.DESCRIPTION,
    subDescription: defaultValues.DESCRIPTION,
    imgFn: defaultValues.PRODUCT_SQUARE_IMAGE,
    src: defaultValues.PRODUCT_SQUARE_IMAGE,
    buttonTitle: 'Shop Now',
    buttonSrc:'/',
    type: '1',
  }
}

export default Products_3_ImageLeft;
