import React from 'react';
import {defaultValues} from '../../constants';

const initOptions = {
    title: defaultValues.HERO_TITLE,
    show: false,
    src: defaultValues.HERO_IMAGE_PC ,
    srcMB: defaultValues.HERO_IMAGE_MB 
}

const Hero = (props) => {
    let {
        options,
        data,
        i
    } = props

    if(data.options === undefined ){
        props.getOptions(i, initOptions)
    }
    return(
        <div className={'hero '}>
            <img className="pc" src={options.src} alt={options.title + ' Hero'}/>
            <img className="mb" src={options.srcMB} alt={options.title + ' Hero'}/>

            {options.show ? <h1 className="Header-2">{options.title}</h1> : ''}
        </div>
    )
}

Hero.defaultProps = {
    options: {
        title: defaultValues.HERO_TITLE,
        show: false,
        src: defaultValues.HERO_IMAGE_PC ,
        srcMB: defaultValues.HERO_IMAGE_MB 
    }
};

export default Hero;