import React from 'react';
import { defaultValues } from "../../constants";
const initOptions = {
    title: defaultValues.HERO_TITLE,
    description: defaultValues.DESCRIPTION,
    type: '1',
    types:{
        1: '',
        2: '',
        3: '',
    }
}

const HeadingDivider = (props) =>{
    let {
        data,
        i,
        options
    } = props;

    let header ={
        1: 'Header-1',
        2: 'Header-1',
        3: '',
    }
    if(data.options === undefined ){
        props.getOptions(i, initOptions)
    }

    return(
        <div id={options.title.toLowerCase().replace(/ /g, '-')} className={"divider-container divider-type-" + options.type } >
                <div className="divider-header">
                    <h3 className={header[options.type]}>
                        {options.title}
                    </h3>
                </div>
                <p>{options.description}</p>
        </div>
    )
}

HeadingDivider.defaultProps =  {
    options:{
        title: defaultValues.HERO_TITLE,
        description: defaultValues.DESCRIPTION,
        type: '3',
    }
};

export default HeadingDivider;
