import React from "react";
import "./ProductFeature.scss";
import { defaultValues } from "../../constants";
import TempImage from "../../TempImage";

const defaultProps = {
  title: defaultValues.HERO_TITLE,
  description: defaultValues.DESCRIPTION,
};

const ProductFeature = (props) => {
  let { title, description, className } = props;
  return (
    <div className={"product-feature " + (className || '')}>
      <TempImage scale="136px" className="temp-img-fn" alt={title + ' Function'}/>
      <p className="Body-1-Caption prod-feat-title">{title}</p>
      <p className="Body-1-Regular">{description}</p>
    </div>
  );
};

ProductFeature.defaultProps = defaultProps;

export default ProductFeature;
