import React from 'react';
import * as UI from '../../../components/all';

const PageList = (props) =>{
    return(
        <div className="feature-page">
            {props.pageLog.map((item,i) => {
               let ItemType = UI[item.el]
               return (
               <div  onClick={()=> props.onItemEdit(i)} key={i}>
                    {item.options ?
                        <React.Fragment>
                            <ItemType getOptions={props.getOptions} data={item} options={item.options} i={i} key={i}/>
                        </React.Fragment>
                        :
                        <ItemType  getOptions={props.getOptions} data={item}  i={i} key={i}/>
                    }
               </div>)
            })} 

        </div>
    )
}

export default PageList;