import React from 'react';
import {defaultValues} from '../../constants';

const defaultProps = {
    item: {
        href: '/',
        description: 'this is a description',
        src: defaultValues.MENU_ITEM_IMAGE,
        label:defaultValues.MENU_ITEM_LABEL,
    },
    type: 1
};

const MenuItem = (props) => {
    let {
        item,
        type
    } = props

    let header = {
        1:'Body-1-Bold',
        2:'Header-1',
        3:'Body-1-Medium',
        4:'Body-1-Medium',
    }
    return (
        <li className="menu-item" >
            <a href={item.href} data-label={item.label.toLowerCase().replace(/ /g, '_')} className="js-tracking">
                {/* eslint-disable-next-line  */}
                {type == 3 || type == 4 ? null :<img src={item.src} alt={item.label + ' image'}  />}
                <div>
                    <p className={header[type]}>{item.label}</p>
                    {item.description && type === 1? <p className="Body-1-Regular menu-caption">{item.description}</p>: ''}
                </div>
            </a>
        </li>
    )
}

MenuItem.defaultProps = defaultProps;

export default MenuItem;