import React from 'react';
import { defaultValues } from "../../constants";


const initOptions = {
    title: defaultValues.HERO_TITLE,
    description: defaultValues.DESCRIPTION,
    type: '1',
    caption: defaultValues.CAPTION,
    src: defaultValues.MENU_ITEM_IMAGE,
    types:{
        1: '',
        2: '',
        3: '',
        4: ''
    }
}

const AboutDescription = (props) =>{
    let {
        data,
        i,
        options
    } = props;

    let header ={
        1: 'Body-1-Bold',
        2: 'Header-2',
        3: 'Title-2',
        4: 'Header-2'
    }

    if(data.options === undefined ){
        props.getOptions(i, initOptions)
    }

    return(
        <div className={"description-container description-type-" + options.type } >
            {/* eslint-disable-next-line  */}
            {options.type == 4 ?
                <img src={options.src} alt={options.title + ' about'}/>
            :
                null
            }
            <div>
                <h3 className={header[options.type]}>
                    {options.title}
                </h3>
                <p>
                    {options.description}
                </p>
                <p className="Body-1-Caption">{options.caption}</p>
            </div>
        </div>
    )
}

AboutDescription.defaultProps = {
    options:{
        title: defaultValues.HERO_TITLE,
        description: defaultValues.DESCRIPTION,
        type: '1',
        caption: defaultValues.CAPTION,
        src: defaultValues.MENU_ITEM_IMAGE
    }
  };

export default AboutDescription;
