import React from 'react';
import { defaultValues } from "../../constants";


const initOptions = {
    src: defaultValues.PRODUCT_SQUARE_IMAGE,
    title: defaultValues.HERO_TITLE,
    subtitle: defaultValues.SECTION_TITLE,
    description: defaultValues.DESCRIPTION,
    numOfCards: 4
}


const InfoCard = (props)=>{
    let {
        data,
        i,
        options
    } = props;
    
    
    if(data.options === undefined ){
        props.getOptions(i, initOptions)
    }
    let cards = [];
    for(var j = 0 ; j < options.numOfCards ;j++){
        cards.push(
            <article key={j} className="info-card">
                <a
                    href="/"
                    className="js-tracking"
                    data-label=""
                >
                    <h4 className="Header-4">{ j.toString().length > 1 ? (j + 1) : '0' + (j + 1)}</h4>
                    <p className="Body-1-Caption subtitle">
                        {options.subtitle}
                    </p>
                    <div className="info-card-Cont">
                        <img
                            src={options.src}
                            alt={options.title}
                        />
                        <p className="Body-1-Caption">
                            {options.description}
                        </p>
                    </div>
                </a>
            
            </article>
        )
    }
    return(
        <div className="info-cards-container">
                {cards.map((card, j) => {
                    return card
                })}
               
            
        </div>
   )
}
InfoCard.defaultProps =  {
    options:{
        src: defaultValues.PRODUCT_SQUARE_IMAGE,
        title: defaultValues.HERO_TITLE,
        subtitle: defaultValues.SUBTITLE,
        description: defaultValues.DESCRIPTION,
        numOfCards: 4
    }
};


export default InfoCard